@mixin small-text {
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: #89939A;
}

@mixin Body-text-14 {
font-weight: 500;
font-size: 14px;
line-height: 21px;
color: #89939A;
}

@mixin Small-text-12 {
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: #89939A;
}

@mixin logo-underheading {
  font-size: 9px;
    text-align: center;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.7px;
    color: #89939A;
    text-align: center;
}
