.slider-container {
  position: relative;
  display: flex;
  width: 1040px;
  margin: 40px auto 0;
  overflow: hidden;

  &__btn {
    position: absolute;
    width: 30px;
    height: 400px;
    border: 1px solid #B4BDC4;
  }

  &__btn--back {
    left: -45px;

  }

  &__btn--next {
    right: -45px;
    top: 0;
  }
}

.slider {
  display: flex;
  width: 1040px;
  height: 400px;
  margin: 0 auto;
  transition: 0.9s;
}

.slide {
  &__img {
    width: 1040px;
    height: 100%;
    object-fit: cover;
  }
}

